<template>
  <li class="cart__item product" >
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120"  alt="item.product.title">
    </div>

    <h3 class="product__title">
      {{ item.product.title }}
    </h3>

    <p class="product__info">
      Объем: <span>128GB</span>
    </p>

    <span class="product__code">
      {{ item.product.id }}
    </span>

    <ProductChangeAmount :amount.sync="amountStore"/>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import {mapMutations, mapActions} from "vuex";
import ProductChangeAmount from "@/components/ProductChangeAmount";

export default {
  name: "CartItem",
  props: ['item'],
  components: {ProductChangeAmount},
  filters: {
    numberFormat
  },
  computed: {
    amountStore: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('changeCartProductAmount', {productId: this.item.productId, amount: value})
      }
    }
  },
  methods: {
    ...mapMutations({deleteProduct: 'deleteCartProduct'}),
    // Равноценно:
    /*deleteProduct(productId) {
      this.$store.commit('deleteCartProduct',  productId)
    }*/
    deleteProduct(productId) {
      this.$store.dispatch('deleteProduct', {productId});
    },
    // Равноценно: 1.
    /*...mapActions({deleteProduct: 'deleteProduct'}),*/
    // Равноценно: 2.
    /*...mapActions(['deleteProduct']),*/
  },
}
</script>
