<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ catalogProductsCount }}
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"/>

      <section class="catalog">
        <div v-if="productsLoading">
          <img src="img/svg/spinner.gif">
        </div>

        <div v-else-if="productsLoadingFailing">
          Произошла ошибка при загрузке товаров.
          <button @click.prevent="loadProducts">Попробовать ещё раз</button>
        </div>

        <ProductsList :products="products" />    <!-- prop-drills >>> @goToPage="(pageName, pageParams) => $emit('goToPage', pageName, pageParams)"-->

        <BasePagination v-model="page" :count="countAllProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
  import products from '@/data/products';
  import ProductsList from '@/components/ProductsList.vue';
  import BasePagination from '@/components/BasePagination';
  import ProductFilter from "@/components/ProductFilter";
  import axios from "axios"
  import {API_BASE_URL} from "@/config";

  export default {
    name: "MainPage",
    components: {ProductFilter, ProductsList, BasePagination},
    data() {
      return {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColorId: 0,
        page: 1,
        productsPerPage: 3,

        productData: null,

        productsLoading: false,
        productsLoadingFailing: false,
      }
    },
    computed: {
      // Фильтрация товаров из локального справочника
      /*filteredProducts() {
        let filteredProducts = products;

        if(this.filterPriceFrom > 0) {
          filteredProducts = filteredProducts.filter(products => products.price > this.filterPriceFrom)
        }

        if(this.filterPriceTo > 0) {
          filteredProducts = filteredProducts.filter(products => products.price < this.filterPriceTo)
        }
        if(this.filterCategoryId) {
          filteredProducts = filteredProducts.filter(products => products.categoryId === this.filterCategoryId)
        }

        if(this.filterColorId) {
          filteredProducts = filteredProducts.filter(products => products.colorsId.includes(this.filterColorId))
        }
        return filteredProducts;
      },*/

      products() {
        // Товары из локального справочника
        /*const offset = (this.page - 1) * this.productsPerPage
        return this.filteredProducts.slice(offset, offset + this.productsPerPage);*/
        //Товары из API
        return this.productData
            ? this.productData.items.map(product => {
              return {
                ...product,
                image: product.image.file.url
              }
            })
            : [];
      },
      catalogProductsCount() {
        let count = this.countAllProducts;

        switch (count) {
          case 1:
          case 21:
          case 31:
            return `${this.countAllProducts} товар`
            break;
          case 2:
          case 22:
          case 32:
          case 3:
          case 23:
          case 33:
          case 4:
          case 24:
          case 34:
            return `${this.countAllProducts} товара`
            break;
          default:
            return `${this.countAllProducts} товаров`
        }
      },
      countAllProducts()  {
        return this.productData ? this.productData.pagination.total : 0;
      }
    },
    methods: {
      loadProducts() {
        this.productsLoading = true;
        this.productsLoadingFailing = false;
        clearTimeout(this.loadProductsTimer);
        this.loadProductsTimer = setTimeout(() => {
          axios.get(
              API_BASE_URL + '/api/products',
              { params: {
                  page: this.page,
                  limit: this.productsPerPage,
                  categoryId: this.filterCategoryId,
                  minPrice: this.filterPriceFrom,
                  maxPrice: this.filterPriceTo,
                  colorId: this.filterColorId

                }
              }
          )
              .then(response => this.productData = response.data)
              .catch(() => this.productsLoadingFailing = true)
              .then(() => this.productsLoading = false)
        }, 0)
      },
    },
    watch: {
      page() {
        this.loadProducts();
      },
      filterPriceFrom() {
        this.loadProducts();
      },
      filterPriceTo() {
        this.loadProducts();
      },
      filterCategoryId() {
        this.loadProducts();
      },
      filterColorId() {
        this.loadProducts();
      },
    },
    created() {
      this.loadProducts();
    }
  }
</script>
