<template>
  <main class="content container" v-if="productLoading">
    <img src="img/svg/spinner.gif">

    <p>Загрузка товара...</p>
  </main>

  <main class="content container" v-else-if="!productData">Не удалось загрузить товар</main>

  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">               <!-- <router-link :to="{name: 'main'}" <<< равноценно >>> <a @click.prevent="$router.push({name: 'main'})-->
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" >
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image.file.url" :alt="product.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>

        <h2 class="item__title">
          {{ product.title }}
        </h2>

        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | numberFormat }}
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>

              <ul class="colors colors--black">
                <li class="colors__item" v-for="color in product.colors" :key="color.id">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" :value="color.code" :name="`color-${product.id}`" >

                    <span class="colors__value" :style="{'background-color': color.code}"></span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <fieldset class="form__block" v-if="category.id === 3">
              <legend class="form__legend">Объемб в ГБ:</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">

                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">

                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128" checked="">

                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <ProductChangeAmount :amount.sync="productAmount"/>

              <button class="button button--primery" type="submit" :disabled="productSending">
                В корзину
              </button>

              <div v-show="productAdded">Товар успешно добавлен в корзину</div>

              <div v-show="productSending">Добавляем товар в корзину...</div>
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              Описание
            </a>
          </li>

          <li class="tabs__item">
            <a class="tabs__link" href="#">      <!--href="#features"-->
              Характеристики
            </a>
          </li>

          <li class="tabs__item">
            <a class="tabs__link" href="#">      <!--href="#guarantee"-->
              Гарантия
            </a>
          </li>

          <li class="tabs__item">
            <a class="tabs__link" href="#">      <!--href="#payment"-->
              Оплата и доставка
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
            Синхронизация со смартфоном<br>
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
            Поддержка сторонних приложений<br>
          </p>

          <a href="#">
            Все характеристики
          </a>

          <h3>Что это?</h3>

          <p id="features">
            Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет оптимизировать свои велотренировки, сделав их максимально эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками по ANT+, объединяя полученную с них информацию. Данные отображаются на дисплее, а также сохраняются на смартфоне. При этом на мобильное устройство можно установить как фирменное приложение, так и различные приложения сторонних разработчиков. Велокомпьютер точно отслеживает местоположение, принимая сигнал с целого комплекса спутников. Эта информация позволяет смотреть уже преодоленные маршруты и планировать новые велопрогулки.
          </p>

          <h3>Дизайн</h3>

          <p id="design">
            Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют всего 74,6 x 47,3 x 22,1 мм. что не превышает габариты смартфона. Корпус гаджета выполнен из черного пластика. На обращенной к пользователю стороне расположен дисплей диагональю 56 мм. На дисплей выводятся координаты и скорость, а также полученная со смартфона и синхронизированных датчиков информация: интенсивность, скорость вращения педалей, пульс и т.д. (датчики не входят в комплект поставки). Корпус велокомпьютера имеет степень защиты от влаги IPX7. Это означает, что устройство не боится пыли, а также выдерживает кратковременное (до 30 минут) погружение в воду на глубину не более 1 метра.
          </p>

          <h3>Гарантии</h3>

          <p id="guarantee">
            При соблюдении правил пользования продуктом гарантия 5 лет.
          </p>

          <h3>Оплата и доставка</h3>

          <p id="payment">
            Во время оформления заказа вы увидите доступные способы оплаты для вашего заказа. Некоторые способы могут быть недоступны для выбранного региона или способа доставки.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
/*import products from "@/data/products";
import categories from "@/data/categories";
import dataColors from "@/data/colors";*/
import ProductChangeAmount from "@/components/ProductChangeAmount";
import goToPage from "@/helpers/goToPage";
import numberFormat from "@/helpers/numberFormat";
import { mapGetters} from 'vuex';
import axios from "axios";
import {API_BASE_URL} from "@/config";
import {mapActions} from "vuex"

export default {
  name: "ProductPage",
  components: {ProductChangeAmount},
  data() {
    return {
      productAmount: 1,

      productData: null,

      productLoading: false,
      productLoadingFailing: false,

      productAdded: false,
      productSending: false
    }
  },
  filters: {
    numberFormat
  },
  computed: {
    product() {
      // используется при использовании локального списка товаров
      /*return products.find(product => product.id === parseInt(this.$route.params.id))*/
      // используется при использовании списка товаров на сервере
      return this.productData;
    },
    category() {
      // используется при использовании локального списка товаров
      /*return categories.find(category => category.id === this.product.categoryId)*/
      // используется при использовании списка товаров на сервере
      return this.productData.category;
    },
    ...mapGetters({
      products: 'cartDetailProduct',
      totalPrice: 'cartTotalPrice',
      productsCount: 'cartProductsCount',
    }),
  },
  methods: {
    ...mapActions(['addProductToCart']),
    // используется при использовании локального списка товаров??
   /* productColor(id) {
      return dataColors.find(el => el.id === id).value;
    },*/
    goToPage,
    addToCart() {
      this.productAdded = false;
      this.productSending = true;
      this.addProductToCart({productId: this.product.id, amount: this.productAmount})
        .then( () => {
          this.productSending = false;
          this.productAdded = true;
        });
    },
    /*loadProduct() {
      this.productLoading = true;
      this.productLoadingFailing = false;
      return axios.get(API_BASE_URL + '/api/products/' + this.$route.params.id)
          .then(response => this.productData = response.data)
          .catch(() => this.productLoadingFailing = true)
          .then(() => this.productLoading = false)
    }*/
    loadProduct() {
      this.productLoading = true;

      this.productLoadingFailing = false;

      clearTimeout(this.loadProductTimer);

      this.loadProductTimer = setTimeout(() => {
        return axios.get(API_BASE_URL + '/api/products/' + this.$route.params.id)
            .then(response => this.productData = response.data)
            .catch(() => this.productLoadingFailing = true)
            .then(() => this.productLoading = false)
      }, 0)
    },
  },
  // Один вариант:
  /*created() {
    this.loadProduct();
  },
  watch: {
    '$route.params.id'() {
      this.loadProduct();
    }
  }*/
  // Второй вариант
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true
    }
  }
}
</script>
