<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" :disabled="amount === 1" @click.prevent="counterDecrease">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" name="count"  v-model.number="amount" @input="$emit('update:amount', +$event.target.value)">

    <button type="button" aria-label="Добавить один товар" @click.prevent="counterIncrease">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
  export default {
    name: "ProductChangeAmount",
    model: {
      prop: 'amount',
      event: 'update:amount'
    },
    props: ['amount'],
    methods: {
      counterDecrease() {
        if (this.amount > 1) {
          this.$emit("update:amount", this.amount - 1);
        }
      },
      counterIncrease() {
        this.$emit("update:amount", this.amount + 1);
      },
    },
  }
</script>


