<template>
  <main v-if="$store.state.cartLoading">
    <img src="img/svg/spinner.gif">

    <p>Загрузка корзины...</p>
  </main>

  <main v-else-if="$store.state.cartLoadingFailing">
    <p>Не удалось загрузить корзину</p>
  </main>


  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>

      <span class="content__info">
        {{ productsCount }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
        </div>

        <div class="cart__block" v-if="parseInt(productsCount) !== 0">
          <p class="cart__desc">
            Мы посчитаем стоимость доставки на следующем этапе
          </p>

          <p class="cart__price">
            Итого: <span>{{ totalPrice |numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import numberFormat from "@/helpers/numberFormat";
  import { mapGetters} from 'vuex';
  import goToPage from "@/helpers/goToPage";
  import CartItem from "@/components/CartItem";

  export default {
    name: 'CartPage',
    components: {CartItem},
    filters: {
      numberFormat
    },
    data() {
      return {
        cartLoading: false,
        cartLoadingFailing: false,
      }
    },
    computed: {
      ...mapGetters({
        products: 'cartDetailProduct',
        totalPrice: 'cartTotalPrice',
        productsCount: 'cartProductsCount'
      }),
    },
    methods: {
      goToPage,
    }
  }
</script>
