import Vue from "vue";
import Vuex from "vuex";
/*import products from "@/data/products";*/
import axios from "axios";
import {API_BASE_URL} from "@/config";

Vue.use(Vuex);

export  default new Vuex.Store({
    state: {
        cartProducts: [
            // Неактуально, т.к. данные теперь приходят по API
            /*{
                productId: 1,
                amount: 2
            }*/
        ],
        userAccessKey: null,
        cartProductsData: [],
        cartLoading: false,
        cartLoadingFailing: false,
        orderInfo: null
    },
    mutations: {
        // Для работы с локальным хранилищем
        /*addProductToCart(state, {productId, amount}) {
            const item = state.cartProducts.find(item => item.productId === productId)

            if (item) {
                item.amount += amount;
            } else {
                state.cartProducts.push({
                    productId: productId,
                    amount: amount
                })
            }
        },*/
        // Равноценно:
        /*addProductToCart(state, payload) {
            const item = state.cartProducts.find(item => item.productId === payload.productId)

            if (item) {
                item.amount += payload.amount;
            } else {
                state.cartProducts.push({
                    productId: payload.productId,
                    amount: payload.amount
                })
            }
        }*/
        updateProductAmount(state, {productId, amount}) {
            const item = state.cartProducts.find(item => item.productId === productId);

            if (item) {
                item.amount = amount;
            }
        },
        deleteCartProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity
                }
            })
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData =[];
        },
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
    },
    getters: {
        cartDetailProduct(state) {
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(el => el.product.id === item.productId).product;

                return {
                    ...item,
                    product: {
                        ...product,
                        image: product.image.file.url,
                    }
                }
            })
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetailProduct.reduce((acc, item) => (item.product.price * item.amount) + acc, 0)
        },
        cartProductsCount(state) {
            let count = state.cartProducts.length;

            switch (count) {
                case 1:
                case 21:
                case 31:
                    return `${state.cartProducts.length} товар`
                    break;
                case 2:
                case 22:
                case 32:
                case 3:
                case 23:
                case 33:
                case 4:
                case 24:
                case 34:
                    return `${state.cartProducts.length} товара`
                    break;
                default:
                    return `${state.cartProducts.length} товаров`
            }
        },
        OrderProductsCount(state) {
            let count = state.orderInfo.basket.items.length;

            switch (count) {
                case 1:
                case 21:
                case 31:
                    return `${state.orderInfo.basket.items.length} товар`
                    break;
                case 2:
                case 22:
                case 32:
                case 3:
                case 23:
                case 33:
                case 4:
                case 24:
                case 34:
                    return `${state.orderInfo.basket.items.length} товара`
                    break;
                default:
                    return `${state.orderInfo.basket.items.length} товаров`
            }
        },
    },
    actions: {
        /*loadCart(context) {
            return axios.get(
                API_BASE_URL + '/api/baskets',
                {
                    params: {userAccessKey: context.state.userAccessKey}
                })
                .then(response => {
                        if (!context.state.userAccessKey) {
                        localStorage.setItem('userAccessKey', response.data.user.accessKey);
                        context.commit('updateUserAccessKey', response.data.user.accessKey);
                    }
                    context.commit('updateCartProductsData', response.data.items);
                    context.commit('syncCartProducts')
                })
        },*/

        loadCart(context) {
            context.state.cartLoading = true;

            clearTimeout(this.loadCartTimer);
            this.loadCartTimer = setTimeout(() => {
                return axios.get(
                    API_BASE_URL + '/api/baskets',
                    {
                        params: {userAccessKey: context.state.userAccessKey}
                    })
                    .then(response => {
                         if (!context.state.userAccessKey) {
                            localStorage.setItem('userAccessKey', response.data.user.accessKey);
                            context.commit('updateUserAccessKey', response.data.user.accessKey);
                        }
                        context.commit('updateCartProductsData', response.data.items);
                        context.commit('syncCartProducts');
                    })
                    .catch(() => context.state.cartLoadingFailing = true)
                    .then(() => context.state.cartLoading = false)

            }, 0)
        },
        // Для работы с API
        addProductToCart(context, {productId, amount}) {
            return axios.post(
                API_BASE_URL + '/api/baskets/products', {
                productId: productId,
                quantity: amount
                },
                {
                    params: {userAccessKey: context.state.userAccessKey}
                })
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items);
                    context.commit('syncCartProducts');
                })
        },
        changeCartProductAmount(context, {productId, amount}) {
            context.commit('updateProductAmount', {productId, amount});

            if (amount < 1) {
                return;
            }

            return axios.put(
                API_BASE_URL + '/api/baskets/products', {
                    productId: productId,
                    quantity: amount
                },
                {
                    params: {userAccessKey: context.state.userAccessKey}
                }
            )
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items);
                })
                .catch( () => {
                    context.commit('syncCartProducts');
                })
        },
        deleteProduct(context, {productId}) {
            context.commit('deleteCartProduct', productId);

            return axios.delete(
                API_BASE_URL + '/api/baskets/products',{
                    data: {
                        productId: productId,
                    },
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .catch(() => {
                    context.commit('syncCartProducts');
                })
        },
        loadOrderInfo(context, orderId) {
            return axios.get(
                API_BASE_URL + '/api/orders/' + orderId,
                {
                    params: {userAccessKey: context.state.userAccessKey}
                })
                .then(response => {
                    context.commit('updateOrderInfo', response.data);
                })
        }
    }
});
