<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === 1}" aria-label="Предыдущая страница" @click.prevent="paginatePrev">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>

    <li class="pagination__item" v-for="pageNumber in countPages" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{'pagination__link&--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">         <!--@click="$event.preventDefault(); paginate(pageNumber)" === @click.prevent=" paginate(pageNumber)"-->
        {{ pageNumber }}
      </a>
    </li>

    <li class="pagination__item"  >
      <a class="pagination__link pagination__link--arrow"  :class="{'pagination__link--disabled': page === countPages}" href="#" aria-label="Следующая страница" @click.prevent="paginateNext(page)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
  export default {
    model: {
      prop: 'page',
      event: 'paginate'
    },
    props: ['page', 'count', 'perPage'],
    computed: {
      countPages() {
        return Math.ceil(this.count / this.perPage)
      }
    },
    methods: {
      paginate(page) {
          this.$emit('paginate', page)
      },
      paginateNext(page) {
        if (page < this.countPages) {
          this.$emit('paginate', this.page + 1)
        } else {
          this.$emit('paginate', this.page)
        }
      },
      paginatePrev() {
        if (this.page > 1) {
          this.$emit('paginate', this.page - 1)
        } else {
          this.$emit('paginate', this.page)
        }
      }
    }
  }
</script>