<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'order'}">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>

      <span class="content__info">
        {{ productsCount }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormInput title="ФИО" placeholder="Введите ваше полное имя" v-model="formData.name" :error="formError.name"/>

            <BaseFormInput title="Адрес доставки" placeholder="Введите ваш адрес" v-model="formData.address" :error="formError.address"/>

            <BaseFormInput title="Телефон" placeholder="Введите ваш телефон" v-model="formData.phone" :error="formError.phone" type="tel"/>

            <BaseFormInput title="Email" placeholder="Введите ваш Email" v-model="formData.email" :error="formError.email" type="email"/>

            <BaseFormTextarea title="Комментарий к заказу" placeholder="Ваши пожелания" v-model="formData.comment" :error="formError.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>

            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">

                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">

                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">

                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">

                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" >
            <OrderItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>

            <p>Итого: <b>{{ productsCount }}</b> на сумму <b>{{ totalPrice |numberFormat }} ₽</b></p>
          </div>

          <div v-if="orderSending">

            <img src="img/svg/spinner-order.gif">

            <p>Оформляем заказ...</p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="orderSending" v-else>
            Оформить заказ
          </button>


        </div>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>

          {{ formErrorMessage }}
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormInput from "@/components/BaseFormInput";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import axios from "axios";
import {API_BASE_URL} from "@/config";
import OrderItem from "@/components/OrderItem";
import { mapGetters } from "vuex";
import numberFormat from "@/helpers/numberFormat";

export default {
  name: "OrderPage",
  components: {BaseFormInput, BaseFormTextarea, OrderItem},
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderSending: false
    }
  },
  filters: {
    numberFormat
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProduct',
      totalPrice: 'cartTotalPrice',
      productsCount: 'cartProductsCount'
    })
  },
  methods: {
    /*order() {
      this.formError = {};
      this.formErrorMessage ='';

      axios.post(API_BASE_URL + '/api/orders',
          {
            ...this.formData
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey
            }
          })
          .then(response => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({name: 'orderInfo', params:{id: response.data.id}});
          })
      .catch(error => {
        this.formError = error.response.data.error.request || {};
        this.formErrorMessage = error.response.data.error.message;
      })
    },*/
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderSending = true;
      clearTimeout(this.orderTimer);
      this.orderTimer = setTimeout(() => {
        axios.post(API_BASE_URL + '/api/orders',
            {
              ...this.formData
            },
            {
              params: {
                userAccessKey: this.$store.state.userAccessKey
              }
            })
            .then(response => {
              this.orderSending = false;
              this.$store.commit('resetCart');
              this.$store.commit('updateOrderInfo', response.data);
              this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
            })
            .catch(error => {
              this.orderSending = false;
              this.formError = error.response.data.error.request || {};
              this.formErrorMessage = error.response.data.error.message;
            })
      }, 0)
    },
  }
}
</script>
